body{
    background-color: plum;
    font-family: Verdana, Geneva, Tahoma, sans-serif;
}
.container{
    width: 85%;
    box-sizing: border-box;
    padding: 10px 20px 20px 20px;
    background-color: white;
    box-shadow: 0px 3px 8px 0px black;
    margin: 50px auto 0 auto;
}
h3{
    font-weight:normal;
}
button{
    width: 100%;
    background-color: rgb(207, 101, 207);
    border: 0;
    padding: 4px;
    color: white;
}
button:hover{
    background-color: rgb(160, 49, 160);
    cursor: pointer;
}
button:active{
    background-color: plum;
}
img{
    width:60px;
    /* height: 100%; */
    float: left;
    border-radius: 50%;
}
div p{
    padding: -10px;
}